<template>
<div>
  <!-- Nav tabs -->
  <ul class="nav nav-tabs" role="tablist" id="myTab">
    <li role="presentation" class="active"><a href="#no_pay" aria-controls="no_pay" role="tab" data-toggle="tab" @click="no_pay"></a></li>
    <li role="presentation"><a href="#wait_deliver_goods" aria-controls="wait_deliver_goods" role="tab" data-toggle="tab" @click="wait_deliver_goods"></a></li>
    <li role="presentation"><a href="#wait_confirm" aria-controls="wait_confirm" role="tab" data-toggle="tab" @click="wait_confirm"></a></li>
    <li role="presentation"><a href="#finish" aria-controls="finish" role="tab" data-toggle="tab" @click="finish"></a></li>
  </ul>
  <!-- Tab panes -->

  <div class="tab-content">
    <!--  -->
    <div role="tabpanel" class="tab-pane active" id="no_pay">
        <table class="table table-bordered">
          <tr class="info">
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <!-- <th></th> -->
            <th></th>
          </tr>
          <tr v-for="data in list" :key="data.outTradeNo">
              <td>{{ data.outTradeNo}}</td>
              <td>{{ data.createTime}}</td>
              <td>{{ data.uname}}</td>
              <td>{{ data.totalFee / 100}}</td>
              <!-- <td>{{ data.status }}</td> -->
              <td>
                <button class="btn btn-default" @click="orderDeail(data.outTradeNo,data.status)"></button>&nbsp;&nbsp;
                </td>
          </tr>
        </table>
    </div>
    <!--  -->
    <div role="tabpane2" class="tab-pane" id="wait_deliver_goods">
      <table class="table table-bordered">
          <tr class="info">
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          <tr v-for="data in list" :key="data.outTradeNo">
              <td>{{ data.outTradeNo}}</td>
              <td>{{ data.createTime}}</td>
              <td>{{ data.uname}}</td>
              <td>{{ data.totalFee / 100}}</td>
              <td>
                <button class="btn btn-default" @click="orderDeail(data.outTradeNo,data.status)"></button>&nbsp;&nbsp;
                <button class="btn btn-primary" @click="deliverGoods(data.outTradeNo)"></button>
                </td>
          </tr>
        </table>
    </div>
    <!--  -->
    <div role="tabpane3" class="tab-pane" id="wait_confirm">
      <table class="table table-bordered">
          <tr class="info">
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          <tr v-for="data in list" :key="data.outTradeNo">
              <td>{{ data.outTradeNo}}</td>
              <td>{{ data.createTime}}</td>
              <td>{{ data.uname}}</td>
              <td>{{ data.totalFee / 100}}</td>
              <td>
                <button class="btn btn-default" @click="orderDeail(data.outTradeNo,data.status)"></button>&nbsp;&nbsp;
                </td>
          </tr>
        </table>
    </div>
    <!--  -->
    <div role="tabpane4" class="tab-pane" id="finish">
      <table class="table table-bordered">
          <tr class="info">
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          <tr v-for="data in list" :key="data.outTradeNo">
              <td>{{ data.outTradeNo}}</td>
              <td>{{ data.createTime}}</td>
              <td>{{ data.uname}}</td>
              <td>{{ data.totalFee / 100}}</td>
              <td>
                <button class="btn btn-default" @click="orderDeail(data.outTradeNo,data.status)"></button>&nbsp;&nbsp;
                </td>
          </tr>
        </table>
    </div>
  </div>
  
  <!--  -->
  <nav aria-label="Page navigation" class='nav-pagination' style="text-align:center;">
        <ul class="pagination" style="display:inline-block;">
          <li>
            <a href="#" aria-label="Previous" :class="{disable: pageNum === 1}" @click="previous">
              <span aria-hidden="true">&laquo;</span>
            </a>
          </li>
           <li v-for='page in totalPages' :key='page' style="cursor: pointer;">
            <a :class="{active2: pageNum === page }" @click="changePage(page)">{{page}}</a>
          </li>
          <li>
            <a href="#" aria-label="Next" @click="next" :class="{disable: pageNum === totalPages}">
              <span aria-hidden="true">&raquo;</span>
            </a>
          </li>
        </ul>
  </nav>
    
</div>
</template>

<script>
import { Toast } from 'mint-ui'
export default {
    data(){
        return {
            pageNum: 1,
            list: [],
            totalPages: '',
            index: '',
        }
    },
    created: function(){
      this.no_pay();
    },
    methods: {
        // 
        no_pay(){
          this.$http.post('admin/api/v1/order/getOrders',{status: 0, pageNum: this.pageNum}).then(result => {
            console.log(result.body.data);
            if(result.body.code == 'OK'){
              this.list = result.body.data.list;
              this.totalPages = result.body.data.pages;
              this.index = 1;
            }else{
              Toast("");
            }
          })
        },
        // 
        wait_deliver_goods(){
           this.$http.post('admin/api/v1/order/getOrders',{status: 1,pageNum: this.pageNum}).then(result => {
            //console.log(result.body.data.list);
            if(result.body.code == 'OK'){
              this.list = result.body.data.list;
              this.totalPages = result.body.data.pages;
              this.index = 2;
            }else{
              Toast("");
            }
          })
        },
        // 
        wait_confirm(){
          this.$http.post('admin/api/v1/order/getOrders',{status: 2,pageNum: this.pageNum}).then(result => {
            //console.log(result.body.data.list);
            if(result.body.code == 'OK'){
              this.list = result.body.data.list;
              this.totalPages = result.body.data.pages;
              this.index = 3;
            }else{
              Toast("");
            }
          })
        },
        //  
        finish(){
          this.$http.post('admin/api/v1/order/getOrders',{status: 3,pageNum: this.pageNum}).then(result => {
            console.log(result.body.data.list);
            if(result.body.code == 'OK'){
              this.list = result.body.data.list;
              this.totalPages = result.body.data.pages;
              this.index = 4;
            }else{
              Toast("");
            }
          })
        },
        // 
        orderDeail(outTradeNo,status){
          this.$router.push({ path: '/orderdeail/' + outTradeNo + '/' + status})
        },
        //
        deliverGoods(outTradeNo){
          this.$http.post('admin/v1/order/delivered',{ outTradeNo: outTradeNo}).then(result => {
            if(result.body.code == 'OK'){
              Toast('');
              this.wait_deliver_goods();
            }
            
          })
        },
        changePage(page){//
          this.pageNum = page;
          if(this.index == 1){
            this.no_pay();
          }else if(this.index == 2){
            this.wait_deliver_goods();
          }else if(this.index == 3){
            this.wait_confirm()
          }else if(this.index == 4){
            this.finish();
          }
        },
        previous(){//
          if(this.pageNum > 1){
            this.pageNum --;
            if(this.index == 1){
              this.no_pay();
            }else if(this.index == 2){
              this.wait_deliver_goods();
            }else if(this.index == 3){
              this.wait_confirm()
            }else if(this.index == 4){
              this.finish();
            }
          }
        },
        next(){//
          if(this.pageNum < this.totalPages){
            this.pageNum ++;
            if(this.index == 1){
              this.no_pay();
            }else if(this.index == 2){
              this.wait_deliver_goods();
            }else if(this.index == 3){
              this.wait_confirm()
            }else if(this.index == 4){
              this.finish();
            }
        }
      },
    }
}
</script>

<style scoped>
.btn-default {
  background-color: #fff;
  border: 1px solid #ccc;
  font-size: 12px;
}
.table th,
.table td {
  vertical-align: middle;
}
.active2 {
  background-color: #337ab7 !important;
  color: #fff !important;
}
.disable {
  /* pointer-events:none; */
  cursor: not-allowed;
  opacity: 0.7;
}
</style>





